body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Navbar */

.navbar {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .16),
    0 2px 10px 0 rgba(0, 0, 0, .12);
  background-color: #ffbb33;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: .5rem 1rem;
}

.navbar-brand {
  display: inline-block;
  padding-top: .3125rem;
  padding-bottom: .3125rem;
  margin-right: 1rem;
  font-size: 1.25rem;
  line-height: inherit;
  white-space: nowrap;
  color: black;
  font-weight: 500;
}

/* Head text */

.head-text {
  display: flex;
  justify-content: center;
  font-size: 1.25rem;
  margin-bottom: .5rem;
  font-weight: 500;
  line-height: 1.2;
  color: #3E4551;
}

/* Action buttons */

.button-container {
  display: flex;
  justify-content: center;
}

.button-container-sub {
  text-align: center;
  display: inline-block;
}


.button {
  padding: .5rem 1.6rem;
  font-size: .64rem;
  border-radius: 10em;
  color: #fff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .16),
    0 2px 10px 0 rgba(0, 0, 0, .12);
  margin: .375rem;
  border: 0;
  cursor: pointer;
  text-transform: uppercase;
  white-space: normal;
  word-wrap: break-word;
  outline: none;
  transition: color .2s ease-in-out,
    background-color .2s ease-in-out,
    border-color .2s ease-in-out,
    box-shadow .2s ease-in-out;
}

.button:hover {
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, .18),
    0 4px 15px 0 rgba(0, 0, 0, .15);
  outline: 0;
}

.button:disabled {
  color: #fff;
  opacity: .65;
  cursor: auto;
}

.button:disabled:hover {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .16),
    0 2px 10px 0 rgba(0, 0, 0, .12);
}

/* Table */

.table-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 2rem;
}

.table-row-col {
  position: relative;
  width: 50%;
  padding-right: 15px;
  padding-left: 15px;
  text-align: left;
}

table {
  border: 1px solid #dee2e6;
  width: 100%;
  border-collapse: collapse;
}

table th {
  border: 1px solid #dee2e6;
  padding: .75rem;
  border-top: none;
  vertical-align: bottom;
  border-bottom: 2px solid #dee2e6;
}

table td {
  border: 1px solid #dee2e6;
  padding: .75rem;
  border-top: none;
  vertical-align: bottom;
}

table tbody tr {
  cursor: pointer;
}

table tbody tr:nth-child(even) {
  background-color: #f2f2f2;
}

table tbody tr:hover {
  background-color: aliceblue;
}

/* Modal */

.modal {
  display: block;
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  position: relative;
  background-color: #fefefe;
  margin: auto;
  padding: 0;
  border: 1px solid #888;
  width: 40%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  animation-name: animatetop;
  animation-duration: 0.4s
}

@-webkit-keyframes animatetop {
  from {
    top: -300px;
    opacity: 0
  }

  to {
    top: 0;
    opacity: 1
  }
}

@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0
  }

  to {
    top: 0;
    opacity: 1
  }
}

.close {
  color: white;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #dddddd;
  text-decoration: none;
  cursor: pointer;
}

.modal-header {
  padding: 2px 16px;
  background-color: #FFBB33;
  color: white;
  text-align: center;
  margin-bottom: 2rem;
}

.modal-body {
  padding: 2px 16px;
}

/* Form */

input[type=text] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type=submit] {
  width: 100%;
  background-color: #FFBB33;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type=submit]:hover {
  background-color: #edae38;
}

/* Delete */

.warn-words {
  position: relative;
  flex: 1 1 auto;
  padding: 1rem;
  font-size: 1.5rem;
  text-align: center;
  margin: 0;
  font-weight: 500;
}

.delete-btns {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  border-top: 1px solid #dee2e6;
  border-bottom-right-radius: .3rem;
  border-bottom-left-radius: .3rem;
}

.delete-options {
  color: #fff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .16),
    0 2px 10px 0 rgba(0, 0, 0, .12);
  padding: .84rem 2.14rem;
  font-size: .81rem;
  transition: color .15s ease-in-out,
    background-color .15s ease-in-out,
    border-color .15s ease-in-out,
    box-shadow .15s ease-in-out;
  margin: .375rem;
  border: 0;
  border-radius: .125rem;
  cursor: pointer;
  text-transform: uppercase;
  white-space: normal;
  word-wrap: break-word;
}

.delete-options:hover {
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, .18),
    0 4px 15px 0 rgba(0, 0, 0, .15);
  outline: 0;
}